// pages/login.js

import { useState } from 'react';
import Image from 'next/image';
import { FaGoogle } from 'react-icons/fa';

const LoginPage = () => {
  const [backgroundIndex, setBackgroundIndex] = useState(0);
  const backgrounds = [
    '/images/background1.jpg',
    '/images/background2.jpg',
    '/images/background3.jpg',
  ];

  // Change background every 5 seconds
  setInterval(() => {
    setBackgroundIndex((prevIndex) => (prevIndex + 1) % backgrounds.length);
  }, 5000);

  return (
    <div className="min-h-screen flex">
      {/* Left Section (Background Image) */}
      <div
        className="flex-1 bg-cover bg-center"
        style={{
          backgroundImage: `url(${backgrounds[backgroundIndex]})`,
        }}
      >
        {/* Optional Content */}
      </div>

      {/* Right Section (Form) */}
      <div className="flex-1 bg-white p-10 flex flex-col justify-center items-center">
        <h1 className="text-4xl font-semibold text-gray-800 mb-8">Login</h1>

        {/* Login Form */}
        <form className="w-full max-w-sm">
          <div className="mb-4">
            <label htmlFor="email" className="block text-lg text-gray-700 mb-2">
              Email Address
            </label>
            <input
              type="email"
              id="email"
              name="email"
              className="w-full p-3 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="Enter your email"
            />
          </div>

          <div className="mb-6">
            <label htmlFor="password" className="block text-lg text-gray-700 mb-2">
              Password
            </label>
            <input
              type="password"
              id="password"
              name="password"
              className="w-full p-3 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="Enter your password"
            />
          </div>

          <button
            type="submit"
            className="w-full p-3 bg-blue-500 text-white rounded-md shadow-md hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-500"
          >
            Login
          </button>

          <div className="flex items-center justify-between my-4">
            <span className="text-gray-600 text-sm">Don't have an account?</span>
            <a href="/signup" className="text-blue-500 text-sm font-semibold">
              Sign Up
            </a>
          </div>

          {/* Google Sign-Up */}
          <button
            type="button"
            className="w-full p-3 bg-white border border-gray-300 text-gray-700 rounded-md shadow-sm flex items-center justify-center space-x-2 hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-blue-500"
          >
            <FaGoogle className="text-blue-500 text-xl" />
            <span>Sign Up with Google</span>
          </button>
        </form>
      </div>
    </div>
  );
};

export default LoginPage;
